import { MatAccordion } from '@angular/material/expansion';
import { LoadingController, ModalController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';
import { CommonService } from 'src/app/services/common.service';
import { ToastController } from '@ionic/angular';
import { Router } from '@angular/router';
import { DirectionsPage } from '../directions/directions.page';

@Component({
  selector: 'app-properties',
  templateUrl: './properties.page.html',
  styleUrls: ['./properties.page.scss'],
})
export class PropertiesPage implements OnInit {
  @ViewChild(MatAccordion) accordion: MatAccordion;
  slideOpts = {
    initialSlide: 1,
    speed: 400,
  };
  email = {
    name: null,
    phone: null,
  };
  properties = [
    {
      name: 'Mandela Property',
    },
    {
      name: 'Phola Park Property',
    },
    {
      name: 'Holomisa Property',
    },
    {
      name: 'Rondebult Property',
    },
    {
      name: 'Lombardy West Property',
    },
    {
      name: 'Protea Glen Property',
    },
  ];

  facilities = {
    name: 'Mandela',
    facilities: [
      {
        facility: 'Electric Fence',
        icon: 'flash',
        show: false,
      },
      {
        facility: 'Swimming Pool',
        icon: 'water',
        show: false,
      },
      {
        facility: 'Security Cameras',
        icon: 'videocam',
        show: false,
      },
      {
        facility: 'Electricity Inclusive',
        icon: 'flashlight',
        show: true,
      },
      {
        facility: 'Water',
        icon: 'beaker',
        show: true,
      },
      {
        facility: 'High Wall',
        icon: 'business',
        show: true,
      },
      {
        facility: 'Shared Bathrooms',
        icon: 'woman',
        show: true,
      },
      {
        facility: 'Own Bathroom',
        icon: 'woman',
        show: false,
      },
      {
        facility: 'Shared Kitchen',
        icon: 'fast-food',
        show: true,
      },
      {
        facility: 'Own Kitchen',
        icon: 'fast-food',
        show: true,
      },
      {
        facility: 'Child Friendly Environment',
        icon: 'happy',
        show: true,
      },
      {
        facility: 'Washing Line',
        icon: 'options',
        show: true,
      },
      {
        facility: 'Laundry Facilities',
        icon: 'shirt',
        show: false,
      },
      {
        facility: 'Unlimited Wi-Fi',
        icon: 'wifi',
        show: false,
      },
      {
        facility: 'Parking',
        icon: 'car-sport',
        show: true,
      },
      {
        facility: 'Ground Floor',
        icon: 'home',
        show: true,
      },
    ],
  };
  availableProperties: [];
  foundProperties = false;
  loader;
  role;
  user;
  menuItems = [
    {
      icon: 'home',
      name: 'Home',
      routerLink: './home',
    },
  ];

  constructor(
    private api: ApiService,
    public loadingController: LoadingController,
    private common: CommonService,
    private router: Router,
    public toastController: ToastController,
    public modalController: ModalController
  ) {}

  ngOnInit() {
    this.role = JSON.parse(localStorage.getItem('role'));
    this.user = this.common.getUser();
    this.transformMenu();
  }

  ionViewDidEnter() {
    this.role = JSON.parse(localStorage.getItem('role'));
    this.user = this.common.getUser();
    if (this.menuItems.length <= 1) {
      this.role = JSON.parse(localStorage.getItem('role'));
      this.user = this.common.getUser();
      this.transformMenu();
    }
  }

  ionViewWillLeave() {
    this.menuItems = [
      {
        icon: 'home',
        name: 'Home',
        routerLink: './home',
      },
    ];
    console.log('reset nav');
  }

  logout() {
    localStorage.removeItem('user');
    this.router.navigate(['./home']);
    window.location.reload();
  }
  transformMenu() {
    if (
      JSON.parse(localStorage.getItem('user')) === undefined ||
      JSON.parse(localStorage.getItem('user')) === null
    ) {
      console.log('Guest');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'chat',
          name: 'Contact',
          routerLink: './contact',
        }
      );
    } else if (
      this.user !== null &&
      this.user !== undefined &&
      (this.user.role.id === 'a' || this.user.role.id === 'l')
    ) {
      console.log('admin and landlord con');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'account_circle',
          name: 'Profile',
          routerLink: './profile',
        },
        {
          icon: 'people',
          name: 'Tenants',
          routerLink: './tenants',
        },
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: '',
        }
      );
    } else if (
      this.user !== null &&
      this.user !== undefined &&
      this.user.role.id === 't'
    ) {
      console.log('tenant con');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'chat',
          name: 'Contact',
          routerLink: './contact',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: './queries',
        }
      );
    } else {
      console.log('team');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'account_circle',
          name: 'Profile',
          routerLink: './profile',
        },
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: '',
        }
      );
    }
  }
  checkRoute(route) {
    if (route.name === 'Logout') {
      localStorage.clear();
      this.router.navigate(['./home']);
    } else {
      this.router.navigate([route.routerLink]);
    }
  }

  addNewProperty(property) {
    this.api
      .addProperty('property/addProperties', property)
      .subscribe((res) => {
        console.log(res);
        if (res.length) {
          this.foundProperties = true;
          this.availableProperties = res;
        }
      });
  }

  getAvailableProperties(propertyName) {
    this.presentLoading();
    const query = { 'location.name': propertyName, available: true };
    this.api
      .getAllAvailableProperties('property/getProperties', query)
      .subscribe((res) => {
        console.log(res);
        if (res.length > 0) {
          this.foundProperties = true;
          this.availableProperties = res;
          this.loader.dismiss();
        } else {
          this.loader.dismiss();
          this.availableProperties = res;
          this.foundProperties = false;
        }
      });
  }

  async presentLoading() {
    await this.loadingController
      .create({
        cssClass: 'my-custom-class',
        message: 'Please wait...',
      })
      .then((res) => {
        this.loader = res;
        res.present();
      });
  }

  async presentToast(msg, toastClass) {
    await this.toastController
      .create({
        message: msg,
        cssClass: toastClass,
        duration: 2000,
      })
      .then((res) => res.present());
  }

  filterProperty(propertyName) {
    this.getAvailableProperties(propertyName.detail.value);
  }

  sendEmail(form, room) {
    if (form.invalid) {
      this.presentToast('Please fill in all fields!', 'failed');
      return false;
    } else {
      this.presentLoading();
      form.value.room = room;
      this.api.sendEmail('property/sendEmail', form.value).subscribe((res) => {
        if (!res?.err) {
          this.loader.dismiss();
          this.presentToast('Email Sent Successfully', 'success');
          setTimeout(() => {
            window.location.reload();
          }, 2200);
        } else {
          this.presentToast(res?.err, 'failed');
        }
      });
    }
  }

  updateRent(form, property) {
    if (form.invalid) {
      this.presentToast('Please fill in the rent field!', 'failed');
      return false;
    } else {
      this.presentLoading();
      this.api
        .updatePropertyRent('property/updateRent/' + property._id, form.value)
        .subscribe((res) => {
          if (!res?.err) {
            this.loader.dismiss();
            this.presentToast('Rent Updated Successfully', 'success');
            setTimeout(() => {
              window.location.reload();
            }, 2200);
          } else {
            this.presentToast(res?.err, 'failed');
          }
        });
    }
  }

  async openDirections(property) {
    await this.modalController
      .create({
        component: DirectionsPage,
        componentProps: { property },
      })
      .then((res) => res.present());
  }
}
